.botaoDisponivel{
  margin-bottom: 2em;
}

.botaoDisponivel .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.botaoDisponivel .switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.botaoDisponivel .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.botaoDisponivel .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

.botaoDisponivel .switch input:checked + .slider {
  background-color: #1ACD36;
}

.botaoDisponivel .switch input:focus + .slider {
  box-shadow: 0 0 1px #1ACD36;
}

.botaoDisponivel .switch input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

  


