.mainAgenda{
    flex: 1
  }
  .paginaAgenda{
    width: 100%;
    height: 88%;
    display: flex;
  }
  .calendario{
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    /* background-color: pink; */
  }
  .agendados{
    width: 60%;
    height: 100%;
    /* background-color: red; */
  
  }
  .servicosAgendados{
    width: 90%;
    height: 90%;
    border-radius: .5em;
    border: .15em solid #FF9F1C;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
  }
  .servicosAgendados legend{
    padding: 0 1em;
    margin-left: 3em;
    font-weight: 600;
  }
  .nadaAgendado{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
  }