.sidebar{
  width: 18%;
  height: 100vh;
  background-image: url('../../../assets/img/sidebar.svg');
  background-repeat: repeat;
  background-size: cover;
  color: #c7c7c7;
}
.logo{
  width: 90%;
  height: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo img{
  width: 60%;
}
.nav{
  width: 100%;
  height: 50%;
  overflow: auto;
}
.nav ul{
  margin: 0;
  list-style: none;
}
.nav li{
  /* height: 1em; */
  margin: 1.5em 0;
  display: flex;
  align-items: center;
}
.nav li:nth-child(1)::before{
  background-image: url("../../../assets/icons/sidebarIcons/home.png");
}
.nav li:nth-child(2)::before{
  background-image: url("../../../assets/icons/sidebarIcons/servicos.png");
}
.nav li:nth-child(3)::before{
  background-image: url("../../../assets/icons/sidebarIcons/agenda.png");
}
.nav li:nth-child(4)::before{
  background-image: url("../../../assets/icons/sidebarIcons/historico.png");
}
.nav li::before{
  width: 10%;
  height: 1.1em;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-size: 1.1em;
  margin-right: 0.5em;
}
.nav .select{
  color: #fff;
  /* text-decoration: underline; */
  cursor: pointer;
}
.nav li:hover{
  color: #fff;
  text-decoration: underline;
  cursor: pointer;
}
.terminoNav{
  width: 100%;
  height: 25%;
  overflow: auto;
}
.terminoNav ul{
  margin: 0;
  list-style: none;
}
.terminoNav li{
  margin: 1.5em 0;
  display: flex;
  color: #c7c7c7;
}
.link:nth-child(1) li::before{
  background-image: url("../../../assets/icons/sidebarIcons/contaServiço.png");
}
.link:nth-child(2) li::before{
  background-image: url("../../../assets//icons//sidebarIcons/sair.png");
}
.terminoNav li::before{
  width: 10%;
  height: 1.1em;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-size: 1.1em;
  margin-right: 0.5em;
}
.terminoNav li:hover{
  color: #f1f1f1;
  text-decoration: underline;
  cursor: pointer;
}
.link{
  color: #f1f1f1;
  text-decoration: none;
}
.link:focus{
  outline: none;
}